<template>
	<view>
		个人信息
		<u-button type="primary" text="前后端测试" @click="QQLogin()"></u-button>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations,
		mapActions
	} from 'vuex'
	import {
		test
	} from '@/api/user/user.js'

	export default {
		computed: {
			...mapState(['user'])
		},
		data() {
			return {};
		},
		onLoad() {
			this.setIsLogin(true)
		},
		methods: {
			...mapMutations(['setIsLogin']),
			...mapActions(['login']),
			QQLogin() {
				test().then(res => {
					console.log(res.data)
				})
			}
		}
	}
</script>

